  import LoadingImg from '../../public/loadingImg'
export default {
    name: 'refundFor',
    data(){
        return{
          api: this.GLOBAL.baseURL,
          refundId:'',
          orderInformation:'',
          order_id:''
        }
    },
  components:{
    LoadingImg
  },
  methods:{
      sendDis(){
      this.$router.push({
            path:'/dispute',
            query:{
              information: this.order_id
            }
          })
      },
      getRefund(){
        this.$http.post(this.api + '/RefundDTwo',{
          token: localStorage.getItem('token'),
          refund_id: this.refundId,
          verson:2.0
        })
          .then(res=>{
            if(res.data.code == 1){
              this.orderInformation = res.data.data
              this.order_id = res.data.data.order_id
              console.log(this.orderInformation)
            }else if(res.data.code == 3){
            
                this.getRefund()
             
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
    goBack(){
        this.$router.go(-1)
    }
  },
  mounted(){
      this.getRefund()
  },
  created(){
      this.refundId = this.$route.query.information
  }
    
}



